import { Route, Switch } from "react-router";
import DefaultLayout from "../Layouts/Defaultlayout";
import Loadable from 'react-loadable';
import Loading from 'Commonitem/Loader';

const DefaultRoute = (props) => {

  let AsyncElectricalItemList = Loadable({
    loader: () => import('Views/ElectricalItem/ElectricalItemList'),
    loading: Loading,
  });
  let AsyncLandingPage= Loadable({
    loader: () => import('Views/LangingPage.js/LandingPage'),
    loading: Loading,
  });
  return (
    <DefaultLayout >
      <Switch>
        <Route  exact path="/electronic/item" component={AsyncElectricalItemList}/>
        <Route  exact path='/' component={AsyncLandingPage}></Route>
      </Switch>
    </DefaultLayout>
  );
};
export default DefaultRoute;
