export const admindashboard = (
    <Menu style={{marginTop:"1.5rem",borderRadius:'4px'}}>
      <Menu.Item>
        <Space direction="horizontal" size="large">
          <LogoutOutlined /> 
          <Typography.Text >
            Log Out
          </Typography.Text>
        </Space>
      </Menu.Item>
      <Menu.Item>
        <Space direction="horizontal" size="large">
          <LoginOutlined/> 
          <Typography.Text >
            Sign Up
          </Typography.Text>
        </Space>
      </Menu.Item>
    </Menu>
  );
